@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:wght@600&family=Montserrat:wght@300;400;700&display=swap);
/** FONT IMPORT  **/

/** RESET STYLE  **/
*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
}

/** COLOR AND FONT VARIABLES  **/
:root {
    --primary-color: #f7f3e9;
    --secondary-color: #a3d2ca;
    --third-color: #5eaaa8;
    --fourth-color: #f05945;
    --fifth-color: #ff8474;

    --primary-font: 'Montserrat', sans-serif;
    --secondary-font: 'Crimson Text', serif;
}


/** INITIAL STYLES **/



html {
    font-size: 10px;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-family: var(--primary-font);
}

html, body {
    height: 100%;
    width: 100%;
}

section {
    min-height: 100vh;
}

footer, section::before {
    display: block;
    content: "";
    height: 1rem;
    margin-top: 1rem;
}

/** HAMBURGER MENU STYLE  **/

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }
  
.change .bar2 {opacity: 0;}
  
.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }


/**  SHOPPING LIST STYLES  **/

.shoppingList {
position: absolute;
top: 2.7rem;
-webkit-transform: translate(-50%);
        transform: translate(-50%);
min-width: 50rem;
min-height: 40rem;
border-radius: 1rem;
visibility: hidden;
opacity: 0;
transition: visibility 0s, opacity 0.2s ease-in-out;
background: white;
overflow: hidden;
z-index: 150;
}
 /** HOVER ON SHOPPING CART  **/  
.showShoppingList:hover .shoppingList {
    visibility: visible;
    opacity: 1;
}

@media screen and (max-width: 1024px) {
    .shoppingList {
        min-width: 40rem;
    }
}

@media screen and (max-width: 768px) {
    .shoppingList {
        min-width: 30rem;
    }
}

@media screen and (max-width: 320px) {
    .shoppingList {
        -webkit-transform: translate(-42%);
                transform: translate(-42%);
    }
}


@media screen and (max-width: 280px) {
    .shoppingList {
        min-width: 27rem;
        -webkit-transform: translate(-35%);
                transform: translate(-35%);
    }
}








/** BUTTON WAVE EFFECTS  **/
  
.btn {
    position: relative;
    border: 0;
    outline: none !important;
    transition: 0.2s ease-out;
  }
  
  
  .btn-default {
    color: #fff;
    background-color: #2BBBAD;
  }
  
  .btn-default:hover {
    background-color: #30cfc0;
    color: #fff ;
  }
  
  .waves-effect {
    position: relative;
    cursor: pointer;
    display: inline-block;
    overflow: hidden;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    vertical-align: middle;
    z-index: 1;
    will-change: opacity, transform;
    transition: all 0.3s ease-out;
  }
  
  .waves-effect .waves-ripple {
    position: absolute;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin-top: -10px;
    margin-left: -10px;
    opacity: 0;
    background: rgba(0, 0, 0, 0.2);
    transition: all 0.7s ease-out;
    transition-property: opacity, -webkit-transform;
    transition-property: transform, opacity;
    transition-property: transform, opacity, -webkit-transform;
    -webkit-transform: scale(0);
    transform: scale(0);
    pointer-events: none;
  }
  
  .waves-effect.waves-light .waves-ripple {
    background-color: rgba(255, 255, 255, 0.45);
  }
  
  .waves-effect.waves-red .waves-ripple {
    background-color: rgba(244, 67, 54, 0.7);
  }
  
  .waves-effect.waves-yellow .waves-ripple {
    background-color: rgba(255, 235, 59, 0.7);
  }
  
  .waves-effect.waves-orange .waves-ripple {
    background-color: rgba(255, 152, 0, 0.7);
  }
  
  .waves-effect.waves-purple .waves-ripple {
    background-color: rgba(156, 39, 176, 0.7);
  }
  
  .waves-effect.waves-green .waves-ripple {
    background-color: rgba(76, 175, 80, 0.7);
  }
  
  .waves-effect.waves-teal .waves-ripple {
    background-color: rgba(0, 150, 136, 0.7);
  }
  
  /* Firefox Bug: link not triggered */
  a.waves-effect .waves-ripple {
    z-index: -1;
  }




